
.vdp-datepicker i {
        font-size: 16px;
        padding-left: 4px;
}
.vdp-datepicker i.fa-times {
        font-size: 14.4px;
}
.vdp-datepicker__calendar-button {
        width: 18px;
}
.vdp-datepicker input,
    .user-form .vdp-datepicker input[type='text'] {
    width: 80%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    border: 0px solid transparent !important;
    border-radius: 4px;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}
.user-form .vdp-datepicker input[type='text'] {
         padding-left: 2.4em;
    width: 86%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    border: 0px solid transparent !important;
    border-radius: 4px;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}
.date-picker--cross {
        position: absolute;
}
.date-picker--cross i {
        cursor: pointer;
}
.date-picker {
        width: 95%;
        height: 30px;

        border: none;
        vertical-align: middle;
}
.form-control {
        position: flex;
        vertical-align: middle;
        cursor: pointer;
}
.picker.form-control {
        padding: .1em .6em;
}
@media only screen and (min-width: 992px) {
.search-section .picker.form-control {
            padding: .8em .6em;
}
}
.input-border.form-control {
        border-color: #66afe9;
        outline: 0;
}
@media only screen and (max-width: 991px) {
.vdp-datepicker .vdp-datepicker__calendar {
            box-shadow: 2px 2px 11px;
            z-index: 100;
            padding: 5em 1em;
            position: fixed;
            height: 80%;
            width: 80%;
            top: 0px;
            left: 0px;
            margin: 10%;
}
}
input#datepicker-mobile {
    position: relative;
    padding-left: 2.4em;
    width: 90%;
    height: 34px;
    padding: 6% 7%;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    border: 0px solid transparent !important;
    border-radius: 4px;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}



