
.app-container.white {
        background-color: #fff;
}
.conversation-component.container {
        width: auto;
}
.conversation_chat p,
    .message_text {
        font-family: Poppins;
        font-size: 15px;
        text-align: left;
}
.conversation_chat h2 {
        font-size: 25px;
        margin-top: .2em;
        margin-bottom: 0;
}
.chat_last_connection {
        margin: .4rem 0;
}
.btn-full-width {
        width: 100%;
        margin: 0 0 .8em 0;
}
.message-wrapper {
        text-align: left;
        margin-bottom: .4em;
}
.conversation-component.container {
        margin-bottom: 3rem;
}
.conversation_chat--search > li {
        color: #009ce1;
        cursor: pointer;
}
.conversation_chat--search > li:hover {
        background-color: #eee;
}
.conversation_chat--search li.list-group-item:last-child {
        border-bottom-width: 1px;
}
.conversation-title {
        font-size: 18px;
        font-weight: 900;
        color: black;
        font-family: Poppins;
}
.list-group-item {
        font-size: 14px;
}
.list-group-item.unread,
    .list-group-item.unread:hover,
    .list-group-item.unread:focus {
        background: rgba(254, 153, 0, .25);
}
@media only screen and (min-width: 768px) {
.conversation-title {
            font-size: 18px;
}
.conversation_chat p,
        .message_text {
            font-family: Poppins;
            font-size: 15px;
            text-align: left;
}
.conversation_chat h2 {
            font-size: 22px;
}
.conversation_chat p.chat_last_connection {
            font-size: 13px;
            margin: 0;
}
.app-container {
            background-color: transparent;
}
.conversation_chat,
        .conversation_chat > div,
        .conversation-component.container .row,
        .conversation-component > .row > div,
        .conversation-component > .row > div > div,
        .conversation_list .list-group
        {
            height: 100%;
}
.conversation_chat .list-group-item:nth-child(2) {
            height: calc(100% - 99px);
            overflow-y: auto;
}
.conversation-component.container {
            padding-left: 10px;
            padding-right: 10px;
            overflow-y: hidden;
            height: calc(100vh - 150px);
}
.conversation-component > .row {
            padding-left: 20px;
            padding-right: 20px;
}
.conversation_chat--search {
            height: calc(100% - 66px);
            overflow-y: auto;
            border-bottom: 1px solid #ddd;
}
}
.row {
    margin-left: 0px !important;
    margin-right: 0px !important;
}
li.list-group-item.alert.alert-warning.conversations {
    margin-top: 0em;
}
.converations-loading{
    margin-top: 1em !important;
}



